<template>
  <div class="mt-4">
      <v-card-title>
        <div class="text-h5 font-weight-light">
          {{ data.detailRate.ProductName }}
        </div>
        <v-spacer></v-spacer>
        <v-btn :to="{ name: 'AddProductRate', params: { ProductID }}" color="primary" depressed elevation="2">
          <v-icon>mdi-plus</v-icon>
          Tambah</v-btn>
      </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table :headers="headers" :items="data.dataDetailRate" loading-text="Loading... Please wait" class="elevation-1" :loading="data.loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.RangePinjamanMin`]="{ item }">
            {{ toRupiah(item.RangePinjamanMin > 0 ? item.RangePinjamanMin : '0')}}
        </template>
        <template #[`item.RangePinjamanMax`]="{ item }">
            {{ toRupiah(item.RangePinjamanMax > 0 ? item.RangePinjamanMax : '0')}}
        </template>
        <template #[`item.EffectiveRate`]="{ item }">
            {{ item.EffectiveRate + ' %' }}
        </template>
        <template #[`item.isactive`]="{ item }">
            {{ item.isactive ? 'Active' : 'Non Active' }}
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn class="primary" @click="hapusProductRate(item.id)">
            <v-icon small>
              mdi-delete
            </v-icon> Delete
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
import Utils from '@/utils/util'
export default {
  name: 'ProductPage',
  data: () => ({
    search: '',
    ProductID: '',
    headers: [
      {
        text: 'ProductID',
        align: 'start',
        sortable: false,
        value: 'ProductID'
      },
      { text: 'Pinjaman Min', value: 'RangePinjamanMin', sortable: false, align: 'right' },
      { text: 'Pinjaman Max', value: 'RangePinjamanMax', sortable: false, align: 'right' },
      { text: 'Rate', value: 'EffectiveRate', sortable: false, align: 'center' },
      { text: 'Tenor', value: 'TenorMin', sortable: false, align: 'center' },
      { text: 'Tenor To', value: 'TenorMax', sortable: false, align: 'center' },
      { text: 'Status', value: 'isactive', sortable: false, align: 'center' },
      { text: 'Action', value: 'action', sortable: false, align: 'center' }
    ]
  }),
  mounted: function () {
    this.ProductID = this.$route.params.ProductID
    store.dispatch('product/detail', { ProductID: this.$route.params.ProductID, ProductName: this.$route.params.ProductName })
  },
  computed: {
    ...mapState('product', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('product', [
      'hapusProductRate'
    ]),
    toRupiah (nominal) {
      return Utils.toRupiah(nominal)
    }
  }
}
</script>
